const prefixer = code => '____sharedrop__' + code;
export function setData(code, data) {
    let allData = localStorage.getItem(prefixer(''));

    if (allData) {
        allData = JSON.parse(allData);
        allData[code] = { code, response: data }
    } else {
        allData = { [code]: { code, response: data } }

    }

    localStorage.setItem(prefixer(''), JSON.stringify(allData));


}

export function getData() {
    let allData = localStorage.getItem(prefixer(''));

    if (allData) {
        return Object.values(JSON.parse(allData)).reverse();
    }

}
export function clear() {
    localStorage.setItem(prefixer(''), '{}');
}



var textArea;
export let copy;

function isOS() {
    return navigator.userAgent.match(/ipad|iphone/i);
}

function createTextArea(text) {

    textArea = document.createElement('textArea');
    textArea.value = text;
    document.body.appendChild(textArea);
}

function selectText() {
    var range,
        selection;

    if (isOS()) {
        range = document.createRange();
        range.selectNodeContents(textArea);
        selection = window.getSelection();
        selection.removeAllRanges();
        selection.addRange(range);
        textArea.setSelectionRange(0, 999999);
    } else {
        textArea.select();
    }
}

function copyToClipboard() {
    document.execCommand('copy');
    document.body.removeChild(textArea);
}

copy = function (text) {
    createTextArea(text);
    selectText();
    copyToClipboard();
};
